@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
* {
    padding: 0;
    margin: 0;
}

.App {
    text-align: center;
}
  
.App-header {
    background: linear-gradient(to bottom, #032435, #053043, #073c52, #094a60, #0b576f);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(46px + 2vmin);
    color: white;
}
  
.App-link {
    color: #61dafb;
}
:root {
    --nav-color: rgba(10, 90, 255, 1);
    --nav-font-size: calc(15px + 2vmin);
    --nav-box-shadow: 0px 0px 9px 3px rgba(41, 41, 41, 0.25);
    --navbar-height: 8vh;

    --ds-font: 'Dancing Script', 'cursive';
}

/* SELECTORS */
/* Direct */
nav {
    position: fixed;
    min-height: 8vh;
    min-height: var(--navbar-height);
    justify-content: space-around;
    width: 100%;
    background-color: rgba(10, 90, 255, 1);
    background-color: var(--nav-color);
    display: flex;
    flex-direction: row;
    align-items: center;
    box-shadow: 0px 0px 9px 3px rgba(41, 41, 41, 0.25);
    box-shadow: var(--nav-box-shadow);
    z-index: 1000;
}

nav ul {
    display: flex;
    list-style-type: none;
    justify-content: space-around;
    flex-direction: row;
    width: 30%;
}

nav ul li {
    font-family: 'Dancing Script', 'cursive';
    font-family: var(--ds-font);
    font-weight: bold;
    font-size: calc(15px + 2vmin);
    font-size: var(--nav-font-size);
}

nav ul li a {
    color: white;
    text-decoration: none;
}

.logo {
    margin-top: 9px;
    margin-left: 10px;
    width: calc(15px + 2vmin);
    width: var(--nav-font-size);
    height: auto;
}

.logo-text {
    font-weight: bold;
    color: white;
    font-family: 'Dancing Script', 'cursive';
    font-family: var(--ds-font);
    font-size: calc(15px + 2vmin);
    font-size: var(--nav-font-size);
    text-decoration: none;
}

.collapse {
    display: none;
    cursor: pointer;
}

.collapse div {
    width: 25px;
    height: 2px;
    background-color: white;
    margin: 5px;
}

@media screen and (max-width: 1180px) {
    nav ul {
        width: 50%;
    }
}

@media screen and (max-width: 768px) {
    body {
        overflow-x: hidden;
    }

    nav ul {
        position: absolute;
        right: 0px;
        height: calc( 100vh - 8vh);
        height: calc( 100vh - var(--navbar-height) );
        top: 8vh;
        background-color: rgba(10, 90, 255, 1);
        background-color: var(--nav-color);
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 50%;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
        transition: -webkit-transform 0.5s ease-in;
        transition: transform 0.5s ease-in;
        transition: transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
    }

    nav ul li {
        opacity: 0;
    }

    .collapse {
        display: block;
    }
}

.nav-active {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

@-webkit-keyframes navLinkFade {
    from {
        opacity: 0;
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }

    to {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@keyframes navLinkFade {
    from {
        opacity: 0;
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }

    to {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}
#bio section {
    background: linear-gradient(to bottom, #0b576f, #095b72, #076075, #076478, #08697a);
    height: 100vh;
}

.container {
    max-width: 60%;
    margin: 0 auto;
    text-align: center;
}

.main-h1 {
    color: white;
    font-size: 80px;
}

.name-h1 {
    font-size: 3.5rem;
    color: white;
}

.title-h2 {
    font-size: 2.5rem;
    font-weight: 400;
    color: white;
}

.me-image {
    display: inline-block;
    position: relative;
    width: 200px;
    height: 200px;
}

.me-image img {
    width: auto;
    height: 100%;
    border-radius: 10px;
    border: 3px solid black;
}

.desc-p {
    font-size: 1.5rem;
    color: white;
}

.contact-section {
    background: linear-gradient(to bottom, #0b576f, #095b72, #076075, #076478, #08697a);
    height: 100vh;
}

.contact-h1 {
    color: white;
    font-size: 80px;
}

.form {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 500px;
    height: 500px;
    border: 10px solid black;
    border-radius: 20px;
    color: white;
}

.form h1 {
    margin: 0 0 20px 0;
    font-size: 36px;
}

.form label {
    font-size: 20px;
}
  
.form input {
    display: block;
    padding: 10px;
    margin: 10px 0 10px 0;
    width: 50%;
    border-radius: 10px;
    border: 0
}

.form button {
    font-size: 16px;
    margin: 12px;
    padding: 7px;
    border: 0;
    background-color: black;
    color: white;
    border-radius: 10px;
}
#work section {
    background: linear-gradient(to bottom, #0b576f, #095b72, #076075, #076478, #08697a);
    height: 100%;
}

.port-h1 {
    color: white;
    font-size: 80px;
}

.cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    padding: 20px;
    grid-gap: 40px;
}

.cards a {
    text-decoration: none;
}

.card {
    background-color: blueviolet;
    border-radius: 20px;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0, .18);
}

.card-container {
    position: relative;
    -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 85%, 0% 100%);
            clip-path: polygon(0% 0%, 100% 0%, 100% 85%, 0% 100%);
}

.card-image {
    width: 100%;
    display: block;
    border-radius: 20px 20px 0 0;
}

.details {
    color: white;
}

.details > h3 {
    font-weight: 600;
    font-size: 18px;
}
footer {
    background-color: var(--nav-color);
    text-align: center;
    font-size: var(--nav-font-size);
    font-family: var(--ds-font);
    color: white;
    height: 5vh;
    padding: 10px;
    font-weight: bold;
    box-shadow: var(--nav-box-shadow);
}

footer a {
    text-decoration: none;
    color: white;
}
